import { AnimationClip } from "cc";
import { DIRECTION_ENUM, DIRECTION_ORDER_ENUM, PARAMS_NAME_TYPE } from "../../Enum";
import { StateMachine } from "../Base/SateMachine";
import State from "../Base/State";
import { SubStateMachine } from "../Base/SubStateMachine";


const BASE_URL = 'texture/player/turnleft/'
export default class TurnLeftSubStateMachine extends SubStateMachine {
    constructor(fsm: StateMachine) {
        super(fsm)

        this.stateMachine.set(DIRECTION_ENUM.TOP, new State(fsm, `${BASE_URL}top/turnleft`))
        this.stateMachine.set(DIRECTION_ENUM.BOTTOM, new State(fsm, `${BASE_URL}bottom/turnleft`))
        this.stateMachine.set(DIRECTION_ENUM.LEFT, new State(fsm, `${BASE_URL}left/turnleft`))
        this.stateMachine.set(DIRECTION_ENUM.RIGHT, new State(fsm, `${BASE_URL}right/turnleft`))
    }

    run() {
        const direction = this.fsm.getParams(PARAMS_NAME_TYPE.DIRECTION)
        this.currentSate = this.stateMachine.get(DIRECTION_ORDER_ENUM[Number(direction)])
    }
}